/**
 * 
 * @param editField 
 */
export const convertFieldWidget = (editField) => {
  const widget = editField.widget;
  let realWidget = convertWidget(widget);
  //
  realWidget.rules = validatorAdapter(editField || []);
  return realWidget;
};

/**
 * 
 * @param widget 
 */
export const convertWidget = (widget) => {
  let widgetProcessorElement = widgetProcessor[widget.widgetName];
  let realWidget;
  //
  if (widgetProcessorElement) {
    realWidget = widgetProcessorElement(widget);
  } else {
    realWidget = { ...widget };
  }
  return realWidget;
};

const widgetProcessor = {
  //
  "v-select": (widget) => {
    return {
      ...widget,
      key: widget.key,
      label: widget.text,
      items: widget.model,
      "x-samll": true,
      "item-text": "label",
      "item-value": "value",
      "small-chips": true,
      multiple: widget.multiple,
      dense: true,
      widgetName: "v-autocomplete",
      clearable: true,
    };
  },
  //input
  "v-text-field": (widget) => {
    return {
      ...widget,
      key: widget.key,
      label: widget.text,
      dense: true,
    };
  },
  "v-region": (widget) => {
    return {
      ...widget,
      key: widget.key,
      label: widget.text,
      items: widget.model,
      "x-samll": true,
      "item-text": "label",
      "item-value": "value",
      "small-chips": true,
      multiple: widget.multiple,
      dense: true,
      widgetName: "v-autocomplete",
      clearable: true,
    };
  },
  //
  "v-textarea": (widget) => {
    return {
      ...widget,
      key: widget.key,
      label: widget.text,
      dense: true,
      rows: 1,
    };
  },
  //
  "tree-select": (widget) => {
    return {
      ...widget,
      dense: true,
      model: "",
      label: widget.text,
      treeItem: widget.model,
    };
  },
  "date-picker": (widget) => {
    return {
      ...widget,
      label:widget.text,   
      dense: true,
    };
  },
};

//
export const validatorAdapter = (editField) => {
  const validators = {
    //
    required: required(editField.text),
    trigger:'blur'

  };

  return editField.rules.map((ruleName) => validators[ruleName]);
};

export const required = function(name) {
  return function(v) {
    return (v !== undefined && v !== null && v !== "") || `${name}`;
  };
};
